.header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    height: 8rem;
    padding: 0 1rem 5rem;
    font-family: 'Roboto Mono', monospace;
}

.header-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 3rem;
    max-width: 80rem;
    margin: 0 auto;
}

.header-breadcrumb {
    font-size: 1.2rem;
    font-weight: 700;
    color: var(--purple);
}
.header-nav, .header-nav ul {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.header-nav li, .header-nav ul {
    list-style: none;
}
.header-nav li {
    -webkit-flex: 0 0 auto;
            flex: 0 0 auto;
    margin-left: 1rem;
}
.header-nav a {
    height: 2rem;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    color: #dbdbdb;
    transition: color .4s ease-out;
    line-height: 1;
}

.header-nav a:hover{
    color:var(--purple)
}
.header-nav a svg {
    display: block;
    width: 1.2rem;
    height: 1.2rem;
    fill: var(--purple);
    position: relative;
    top: 2px;
    transition: fill .4s ease-out;
}
.header-nav-button {
    -webkit-flex: 0 0 auto;
            flex: 0 0 auto;
    width: 3rem;
    height: 3rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column nowrap;
            flex-flow: column nowrap;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: .8rem;
    margin-right: -1rem;
    background: none;
    border: 0;
}

.header-nav-button .dots {
    -webkit-flex: 0 0 auto;
            flex: 0 0 auto;
    position: relative;
    width: .25rem;
    height: .25rem;
    border-radius: .25rem;
    background: var(--header-link);
    transition: all .4s cubic-bezier(.19,1,.22,1);
}
@media screen and (max-width: 768px){
