.wrapper{
    font-family: 'Roboto Mono', monospace;
}
.wrapper {
    position: relative;
    z-index: 1;
    padding-bottom: 20vh;
    min-height: 100vh;
    transition: opacity .5s linear;
}
.wrapper:after {
    content: "";
    display: block;
    position: absolute;
    top: 50vh;
    bottom: 50vh;
    left: calc(28vw - 2rem);
    z-index: 99;
    width: 1px;
    opacity: .3;
    background: var(--purple) linear-gradient(
180deg
,var(--bg),rgba(var(--bg-rgb),0) 20vh);
}
.spine, .spine-target {
    position: fixed;
    z-index: 2;
}
.spine {
    top: 50vh;
    bottom: 0;
    left: calc(28vw - 2rem);
    width: 1px;
    background: var(--purple) linear-gradient(
0deg
,var(--bg) 1rem,rgba(var(--bg-rgb),0) 20vh);
}
.spine-target {
    top: calc(50vh - 8px);
    left: calc(28vw - 2rem - 4px);
    width: 9px;
    height: 9px;
 
}

.spine-target .circle, .spine-target .pulse {
    position: absolute;
    width: 9px;
    height: 9px;
    -webkit-transform-origin: center;
            transform-origin: center;
    border-radius: 3rem;
    background: transparent;
    border: 1px solid var(--purple);
}
.spine-target .pulse {
    position: absolute;
    width: 9px;
    height: 9px;
    -webkit-transform-origin: center;
            transform-origin: center;
    border-radius: 3rem;
    background: transparent;
    border: 1px solid var(--purple);
}
